body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 30px;
  padding: 0;
  background-color: rgb(227, 233, 244);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  text-decoration: none;
}
.Header_icon{
  width: 60px;
  height: 60px;
  cursor: pointer;
  object-fit:contain;
}
.Header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgb(119, 143, 72);
  padding: 0px 8vw 0px ;
  position: fixed;
  z-index: 999;
  width: 100%;
}
.Hul{
  margin: 0;
}


nav li:hover{
  background-color: #060;
}
nav{
  margin-right: 125px;
}
nav > ul {
  display: flex;
  list-style: none;

}

nav > ul > li > a {
  display: flex;
  line-height: 20px;
  padding: 12px 2vw;
}

.btntw{
  color: #FFFFFF;
  padding-left: 23px;
}
.btnen{
  color: #b2db93;
  padding-left: 23px;
}


.slider{
  position: relative;
  width: 100%;
  height: 500px;

}
.slider_img{
  width: 100%;
  height: 500px;
  object-fit: cover;
}
.slider_prev{
  position: absolute;
  top: 50%;
  left: 16px;
  transform: translateY(-50%);
  border: none;
  border-radius: 50%;
  background-color: #FFFFFF;
  width: 36px;
  height: 36px;
  cursor: pointer;
}
.slider_next{
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translateY(-50%);
  border: none;
  border-radius: 50%;
  background-color: #FFFFFF;
  width: 36px;
  height: 36px;
  cursor: pointer;
}
.slider_prev img, .slider_next img{
  display: block;
  margin: auto;
}

.slider_slide{
  position:absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 500px;
  opacity: 0;
  transition: opacity 0.25s ease-out;
}

.slider_slide_active{
  opacity: 1;
}


.slider_dots{
  display: flex;
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  
}
.slider_dot{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  margin: 0 4px;
  cursor: pointer;
  border-color: #282036;
  border-width: 2px;
  border-style: solid;

}
.slider_dot::before{
  content: "";
  display: block;
  width: 70%;
  height: 70%;
  background-color: #31243c;
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.5s ease-out;
}
.slider_dot_active::before{
  opacity: 1;
}

.content{
  height: 100%;
  margin-bottom: 100px;
}

.content_container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 800px;
}
.edit{
  height: 100%;
  width: 300px;
  padding-top: 200px;
}
.edit span{
  font-size: 20px;
  font-weight: 600;
  padding-left: 38px;
  
}
.edit li{
  height: 40px;
}
li{
  list-style-type: none;
}

.sale{
  height: 100%;
  width: 80%;
  padding-top: 100px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  
}

.sale_icon{
  width: 240px;
  height: 230px;
  border-radius: 50px;
}

.sale_list{
  transition: .5s;  
  width: 28%;
  text-align: center;
  padding: 1.5vw;
  border: 0px #fff solid;
  margin-bottom: 40px ;
  margin: 0px 0.5%;
}
.sale_list:hover{
  box-shadow: 3px 3px 5px 6px rgb(146, 124, 124);
}
.sale_icon_container{
  width: 100%;
  height: 15vw;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.sale_icon_name{
    display: block;
    padding-top: 10px;
    color: #595757;
    font-size: 16px;
    transition: .5s;

}

.addToCart {
  background-color: transparent;
  border: 2px solid rgb(19, 19, 19);
  min-width: 100px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 15px;
  cursor: pointer;
}



.ft{
  display: flex;
  background-color: rgb(119, 143, 72);
  align-items: center;
  padding: 20px 9vw 10px;
  
}
.ft > .Hlo{
  margin-right: 8%;
}






.cart_container{
  width: 60%;
  margin: auto;
}
.cart_box{
  padding-top: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 2px solid black;
  padding-bottom: 5px;
}
.cart_box img{
  width: 50px;
  height: 50px;
}

.cart_img{
  display: flex;
  width: 400px;
}
.cart_img p {
  font-weight: bold;
  margin-left: 10px;
}


.cart_box div:nth-child(2) button{
  padding: 5px 10px;
  font-weight: bold;
  margin-right: 5px;
}
.cart_box div:nth-child(3) button{
  padding: 5px 10px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  border-radius: 30px;
  background-color: #fff;
  color: black;

}

.cart_box div:nth-child(3) button:hover {
  background-color: gray;
}



.edit_item a{
  color: black;
}

.active_edit_item a{
  color: rgb(219, 92, 7);
}
.cart_img img{
  border-radius: 50px;
}

.cart_size{

}

.aboutus_container{
  width: 100%;
  height: 140vh;
  padding: 90px 0;
  display: flex;
  flex-wrap: wrap;
}
.about_box{
  width: 100%;
  display: flex;
  justify-content: center;
  
  align-items: center;
  margin-bottom: 100px;
}
.about_img{
  height: auto;
  width: 302px;
  height: 300px;
  object-fit: cover;  
  border-radius: 50px;
}
.about_text{
  width: 540px;
  margin-left: 150px;
}


.cart_container{
  height: 1000px;
}